export enum AudioPlayerAudioRowEnum {
    ambient = 'ambient',
    click = 'click',
    // story = 'story',
}

export type AudioPlayerConfigType = {
    audioRow: AudioPlayerAudioRowEnum;
    isLoop: boolean;
    isMuted: boolean;
    isPlaying: boolean;
    isShuffle: boolean;
    trackList: Array<string>;
    volume: number;
};

export type AudioPlayerType = {
    isPlaying: boolean;
    pause: () => void;
    play: () => void;
    setIsMuted: (isMuted: boolean) => void;
    setVolume: (volume: number) => void;
    stop: () => void;
};
