import {useCallback, useMemo, useEffect} from 'react';
import {useDocumentVisibility} from 'react-system-hook';

import {playAudio, PlayAudioArgumentType} from '../hook/audio-player-hook/audio-player-helper';
import {
    AudioPlayerAudioRowEnum,
    AudioPlayerConfigType,
    AudioPlayerType,
} from '../hook/audio-player-hook/audio-player-type';
import {useAudioPlayer} from '../hook/audio-player-hook/audio-player-hook';

import {mediaAudio} from './media';
import {UsePlayAudioType} from './play-media-type';

export function usePlayClick(data: Partial<PlayAudioArgumentType> = {}): UsePlayAudioType {
    const play = useCallback((): Promise<unknown> => {
        return playAudio({
            audioRow: AudioPlayerAudioRowEnum.click,
            isMuted: false,
            src: mediaAudio.click.src,
            volume: 0.5,
            ...data,
        });
    }, [data]);

    const playAudioHook: UsePlayAudioType = useMemo((): UsePlayAudioType => {
        return {
            play,
        };
    }, [play]);

    return playAudioHook;
}

export function usePlayTitleTheme(data: Partial<AudioPlayerConfigType> = {}): AudioPlayerType {
    const audioPlayer: AudioPlayerType = useAudioPlayer({
        audioRow: AudioPlayerAudioRowEnum.ambient,
        isLoop: true,
        isMuted: false,
        isPlaying: false,
        isShuffle: false,
        trackList: [mediaAudio.titleTheme.src],
        volume: 0.5,
        ...data,
    });

    const {play: playTitleTheme, pause: pauseTitleTheme} = audioPlayer;

    const isDocumentVisible = useDocumentVisibility();

    useEffect(() => {
        if (isDocumentVisible) {
            playTitleTheme();
            return;
        }
        pauseTitleTheme();
    }, [playTitleTheme, pauseTitleTheme, isDocumentVisible]);

    return audioPlayer;
}

