/* global document, CanvasRenderingContext2D */

import {GameLandscapeType} from '../../../../../model/game/landscape/landscape-type';
import {SizeType} from '../../../../../model/map/map-type';
import {getLandscapeSize} from '../../../../../model/game/landscape/landscape-helper';
import {squareSize} from '../../client-game-view-container/client-game-view-container-const';
import {GamePlayerType} from '../../../../../model/game/player/player-type';
import {GameBuildingType, GameBuildingTypeEnum} from '../../../../../model/game/building/building-type';
import {getBuildingImage} from '../../client-game-building/client-game-building-helper';

function drawBuildingSquare(
    building: GameBuildingType,
    playerList: Array<GamePlayerType>,
    canvasContext: CanvasRenderingContext2D
) {
    const {x, y, buildingType} = building;

    const buildingImage = getBuildingImage(building, playerList);

    const shiftY = buildingType === GameBuildingTypeEnum.castle ? squareSize : 0;

    canvasContext.drawImage(buildingImage, x * squareSize, y * squareSize - shiftY);
}

export function getBuildingListCanvas(
    landscape: GameLandscapeType,
    playerList: Array<GamePlayerType>,
    buildingList: Array<GameBuildingType>
): string {
    const mapSize: SizeType = getLandscapeSize(landscape);
    const canvas = document.createElement('canvas');
    const canvasContext = canvas.getContext('2d');

    canvas.width = mapSize.width * squareSize;
    canvas.height = mapSize.height * squareSize;

    if (!canvasContext) {
        console.error('canvasContext is not defined');
        return '';
    }

    buildingList.forEach((building: GameBuildingType) => {
        drawBuildingSquare(building, playerList, canvasContext);
    });

    return canvas.toDataURL('image/png', 1);
}
