import {RouterProvider, createHashRouter, RouteObject} from 'react-router-dom';

import {ClientHome} from '../page/client/client-home/client-home';
import {Error404} from '../page/service/error-404/error-404';
import {Page} from '../layout/page/page';
import {ClientLocalGameSetup} from '../page/client/client-local-game-setup/client-local-game-setup';
import {ClientGame} from '../page/client/client-game/client-game';
import {ClientPlay} from '../page/client/client-play/client-play';
import {ClientSkirmish} from '../page/client/client-skirmish/client-skirmish';
import {MapEditor} from '../page/client/map-editor/map-editor';
import {ClientSkirmishUserMap} from '../page/client/client-skirmish/client-skirmish-user-map';
import {ClientSettings} from '../page/client/client-settings/client-settings';
import {ClientInstructions} from '../page/client/client-instructions/client-instructions';
import {ClientAbout} from '../page/client/client-about/client-about';
import {ClientCompanies} from '../page/client/client-companies/client-companies';
import {UnitStore} from '../page/client/client-game/unit-store/unit-store';

import {appRoute} from './app-route';

export function AppRouting(): JSX.Element {
    const routeData: Array<RouteObject> = [
        {
            Component: ClientHome,
            path: appRoute.root.path,
        },
        {
            Component: ClientPlay,
            path: appRoute.play.path,
        },
        {
            Component: ClientSkirmish,
            path: appRoute.skirmish.path,
        },
        {
            Component: ClientSkirmishUserMap,
            path: appRoute.skirmishUserMaps.path,
        },
        {
            Component: ClientLocalGameSetup,
            path: appRoute.setupMap.path,
        },
        {
            Component: ClientGame,
            children: [
                {
                    Component: UnitStore,
                    path: appRoute.unitStore.path,
                },
            ],
            path: appRoute.clientGame.path,
        },
        {
            Component: MapEditor,
            path: appRoute.mapEditor.path,
        },
        {
            Component: ClientSettings,
            path: appRoute.settings.path,
        },
        {
            Component: ClientInstructions,
            path: appRoute.instructions.path,
        },
        {
            Component: ClientAbout,
            path: appRoute.about.path,
        },
        {
            Component: ClientCompanies,
            path: appRoute.companies.path,
        },
        {
            element: (
                <Page>
                    <Error404 />
                </Page>
            ),
            path: '*',
        },
    ];

    const router = createHashRouter(routeData);

    return <RouterProvider router={router} />;
}
