import {useEffect, useState} from 'react';
import {useNavigate} from 'react-router-dom';

import {useGameContext} from '../../../../provider/game/game-context';
import {getIsSkirmishEnd} from '../../../../model/game/player/player-helper';
import {GamePopup} from '../../../../layout/game-popup/game-popup';
import {getIsSkirmishMap} from '../../../../model/map/map-helper';
import {GamePlayerColorIdEnum, GamePlayerType, GamePlayerTypeEnum} from '../../../../model/game/player/player-type';
import gamePopupStyle from '../../../../layout/game-popup/game-popup.scss';
import {Locale} from '../../../../provider/locale/locale-context';
import {UiButton} from '../../../../layout/ui-button/ui-button';
import {AudioPlayerConfigType} from '../../../../hook/audio-player-hook/audio-player-type';
import {defeatSound, gameTheme, victorySound} from '../../../../media/play-media';
import {useAudioPlayer} from '../../../../hook/audio-player-hook/audio-player-hook';

export function SkirmishEndGamePopup(): JSX.Element | null {
    const {gameState} = useGameContext();
    const {playerList, mapId, clientGamePlayerIdList} = gameState;
    const isSkirmish = getIsSkirmishMap(mapId);
    const navigate = useNavigate();
    const [musicPlayerState, setMusicPlayerState] = useState<AudioPlayerConfigType>(gameTheme);

    // const isWinner = clientGamePlayerIdList in playerList
    const isWinner = playerList.some((player: GamePlayerType): boolean => {
        return clientGamePlayerIdList.includes(player.gamePlayerId) && player.playerType === GamePlayerTypeEnum.human;
    });

    const winnerColorList = playerList.map<GamePlayerColorIdEnum>((player: GamePlayerType): GamePlayerColorIdEnum => {
        return player.gamePlayerColorId;
    });

    const isOpen = getIsSkirmishEnd(playerList) && isSkirmish;

    const {play} = useAudioPlayer(musicPlayerState);

    useEffect(() => {
        if (!isOpen) {
            return;
        }

        setMusicPlayerState(isWinner ? victorySound : defeatSound);

        play();
    }, [play, isOpen, isWinner]);

    return (
        <GamePopup color={isWinner && winnerColorList.length > 0 ? winnerColorList[0] : null} isOpen={isOpen}>
            <h3 className={gamePopupStyle.game_popup__header}>
                <Locale stringKey={isWinner ? 'END_GAME__VICTORY' : 'END_GAME__DEFEAT'} />
            </h3>

            <div className={gamePopupStyle.game_popup__footer}>
                <UiButton langKey="UI__OK" onClick={(): void => navigate(-1)} />
            </div>
        </GamePopup>
    );
}
